.contacts {
  &__inner {
    display: flex;
    justify-content: space-between;
  }

  &__body {
  }

  &__content {
    margin: 0 20px;
    transition: transform 0.6s;
  }

  &__list {
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &__item {
    transition: transform 0.4s;
    cursor: pointer;

    &:not(:last-child) {
      margin-right: 15px;
    }

    &:hover {
      transform: scale(1.08);
    }
  }

  &__link {
    font-size: 1rem;
    color: #ececec;
  }

  &__icon {
    max-width: 50px;
    max-height: 50px;
  }
}
