.cards {
  display: flex;
  max-width: 90vw;

  &__item {
    @include bg();
    position: relative;
    flex: 0.5;
    margin: 10px;
    border: 2px solid #000;
    border-radius: 20px;
    min-height: 300px;
    max-width: 500px;
    color: #fff;
    transition: all 0.7s ease-in;
    cursor: pointer;

    &.cards__item--active {
      flex: 5;

      .cards__title {
        opacity: 1;
        transition: opacity 0.4s ease-in 0.4s;
      }

      .cards__link {
        visibility: visible;
      }
    }
  }

  &__title {
    position: absolute;
    left: 20px;
    bottom: 20px;
    border-radius: 5px;
    padding: 5px 7px;
    font-size: 24px;
    opacity: 0;
    background-color: #000;
  }

  &__link {
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    visibility: hidden;
    cursor: pointer;
  }
}
